/* global graphql */

import React from 'react';
import TeamStats from '../components/team-stats';
import Performance from '../components/Performance';
import Chart from '../components/chart';
import Breadcrumb from '../components/breadcrumb';
import { isNullOrUndefined } from 'util';


class IndexPage extends React.Component {
	constructor(props) {
		super(props);
		this.teamChangeHandler = this.teamChangeHandler.bind(this);
		this.rate = this.rate.bind(this);
		this.loadChart = this.loadChart.bind(this);
		this.loadTeamData = this.loadTeamData.bind(this);
		this.state = {
			teamSelected: {},
			allTeams: [],
			allGames: [],
			dataSet: {
				label: "Wins",
				lineTension: 0.3,
				backgroundColor: "rgba(2,117,216,0.2)",
				borderColor: "rgba(2,117,216,1)",
				pointRadius: 5,
				pointBackgroundColor: "rgba(2,117,216,1)",
				pointBorderColor: "rgba(255,255,255,0.8)",
				pointHoverRadius: 5,
				pointHoverBackgroundColor: "rgba(2,117,216,1)",
				pointHitRadius: 50,
				pointBorderWidth: 1,
				data: [],
			}
		}
	}


	teamChangeHandler = (event) => {
		var teams = this.props.data.allDataJson.edges[0].node.owners;

		//find index on state's object by ID
		var splitit = event.target.value.split(" ");
		var newTeam = 0;

		if (splitit[1] === '(inactive)') {
			// update actual state with modified object
			var teaminner = {
				name: splitit[0],
				image: "/img/DEF.png",
			}
			this.setState(
				{ teamSelected: this.loadTeamData(teaminner, this.state.allGames) },
				() => this.loadChart()
			)
		} else {
			newTeam = teams.findIndex(team => {
				return team.name === event.target.value;
			});

			// update actual state with modified object
			this.setState(
				{ teamSelected: this.loadTeamData(teams[newTeam], this.state.allGames) },
				() => this.loadChart()
			)
		}
	}

	rate = (win, loss, tie) => {
		var totalOGames = parseInt(win) + parseInt(loss) + parseInt(tie);
		var winPercent = 100 * parseInt(win);
		if (win != 0) {
			return Math.round(winPercent / totalOGames);
		}
		else {
			return 0;
		}
	}

	loadTeamData = (team, games) => {
		//stats build upon scraped data collection
		
		var teamObject = team;
		var teamGames = [];
		var gamesByYear = [];
		var playOffWeeks = [];
		var TeamPlayOffGames = [];
		var playOffYears = [];
		var TeamPlayOffYears = 0;

		games.map((game) => {
			if ((game.team_a["0"].owner === team.name || game.team_b["0"].owner === team.name)) {
				teamGames.push(game);
			}
		})

		// get games by years and weeks

		games.map((game) => {
			if (gamesByYear.find(obj => obj.year === game.year)) {
				var currentYear = gamesByYear.find(obj => obj.year === game.year)
				if (currentYear.weeks.find(obj => obj.week === game.week)) {

					var currentWeek = currentYear.weeks.find(obj => obj.week === game.week)

					currentWeek.games.push(game);

				} else {
					currentYear.weeks.push({
						week: game.week,
						games: [game],
						year: game.year
					})
				}

			} else {
				gamesByYear.push({
					year: game.year,
					weeks: [{
						week: game.week,
						games: [game],
						year: game.year
					}]
				});
			}
		})

		// mark the playoff weeks and populate array

		gamesByYear.map((year) => {
			var gamesChecker = 0;
			year.weeks.map((week) => {
				if (gamesChecker === 0) {
					gamesChecker = week.games.length;
				} else if (week.games.length < gamesChecker) {
					week.isPlayoff = true;
					playOffWeeks.push(week)
				}
			});
		});

		// get team's playoff games

		playOffWeeks.map((week) => {
			week.games.map((game) => {
				if (game.team_a['0'].owner === team.name || game.team_b['0'].owner === team.name) {
					TeamPlayOffGames.push(game);
				}
			});

			if (playOffYears.find(obj => obj.year === week.year)) {
				var yearObj = playOffYears.find(obj => obj.year === week.year);
				yearObj.games.push(week.games);

			} else {
				playOffYears.push({
					year: week.year,
					games: [week.games]
				})
			}
		});

		TeamPlayOffGames.map((game, index) => {
			if (isNullOrUndefined(TeamPlayOffGames[index - 1])) {
				TeamPlayOffYears++;
			} else {
				if (game.year === TeamPlayOffGames[index - 1].year) {

				} else {
					TeamPlayOffYears++;
				}
			}
		})

		// get playoff stats

		teamObject.playOffWins = 0;
		teamObject.playOffLoses = 0;
		teamObject.playOffTies = 0;
		teamObject.playoffAppearances = 0;
		var playoffAppearances = [];

		TeamPlayOffGames.map((game) => {
			var recordA = game.team_a[0].record.split("-");
			var rankA = recordA[recordA.length - 1].split(" ");
			var rankATrimmed = parseInt(rankA[1].match(/\(([^)]+)\)/)[1]);
			var recordB = game.team_b[0].record.split("-");
			var rankB = recordB[recordB.length - 1].split(" ");
			var rankBTrimmed = parseInt(rankB[1].match(/\(([^)]+)\)/)[1]);

			if (game.team_a['0'].owner === team.name && rankATrimmed < 5 || game.team_b['0'].owner === team.name && rankBTrimmed < 5) {

				if (playoffAppearances.includes(game.year) === false) playoffAppearances.push(game.year);
				if (
					(parseFloat(game.team_a[0].score) > parseFloat(game.team_b[0].score)) && game.team_a[0].owner === team.name ||
					(parseFloat(game.team_b[0].score) > parseFloat(game.team_a[0].score)) && game.team_b[0].owner === team.name
				) {
					teamObject.playOffWins++

				} else if (parseFloat(game.team_b[0].score) === parseFloat(game.team_a[0].score)) {
					teamObject.playOffTies++
				} else {
					teamObject.playOffLoses++
				}
			}
			teamObject.playoffAppearances = playoffAppearances.length;

		});

		teamObject.regularSeasonChampion = 0;
		teamObject.regularSeasonLastPlace = 0;
		teamObject.championshipAppearance = 0;
		teamObject.championshipWins = 0;
		teamObject.championshipLoses = 0;
		teamObject.championshipTies = 0;
		teamObject.yearAppearance = [];
		
		// championship stats 

		playOffYears.map((year) => {
			var appWeek = year.games[year.games.length - 2]
			var topTeam = {};

			appWeek.map((game) => {
				var recordA = game.team_a[0].record.split("-");
				var rankA = recordA[recordA.length - 1].split(" ");
				var rankATrimmed = parseInt(rankA[1].match(/\(([^)]+)\)/)[1]);

				var recordB = game.team_b[0].record.split("-");
				var rankB = recordB[recordB.length - 1].split(" ");
				var rankBTrimmed = parseInt(rankB[1].match(/\(([^)]+)\)/)[1]);

				if (rankATrimmed === 1) {
					topTeam = game.team_a[0]
				}

				if (rankBTrimmed === 1) {
					topTeam = game.team_b[0]
				}

				if (
					(parseFloat(game.team_a[0].score) > parseFloat(game.team_b[0].score)) && game.team_a[0].owner === team.name && rankATrimmed < 5 ||
					(parseFloat(game.team_b[0].score) > parseFloat(game.team_a[0].score)) && game.team_b[0].owner === team.name && rankBTrimmed < 5
				) {
					teamObject.championshipAppearance++
					teamObject.yearAppearance.push(game.year);
				}
			})

			var champWeek = year.games[year.games.length - 1]

			champWeek.map((game, index) => {

				if (
					(parseInt(game.team_a[0].score) > parseInt(game.team_b[0].score)) && game.team_a[0].owner === team.name && teamObject.yearAppearance.includes(game.year) ||
					(parseInt(game.team_b[0].score) > parseInt(game.team_a[0].score)) && game.team_b[0].owner === team.name && teamObject.yearAppearance.includes(game.year)
				) {
					teamObject.championshipWins++
				}
				else {
					teamObject.championshipLoses++
				}
			})
			if (topTeam.owner === team.name) {
				teamObject.regularSeasonChampion++

			}
		})

		//find year of first occurance

		for (var ii = 0; ii < teamGames.length; ii++) {
			const week = teamGames[ii].week.split(" ");
			teamGames[ii].date = "01/" + week[1] + "/" + parseInt(games[ii].year);
		}

		var nikcindex = teamGames.findIndex((game) => {
			if (game.team_a["0"].owner === team.name || game.team_b["0"].owner === team.name) {
				return game.year;
			}
		});

		if (teamGames[nikcindex].team_a['0'].owner === team.name) {
			if (teamGames[nikcindex].team_a['0'].team) {
				teamObject.nickname = teamGames[nikcindex].team_a['0'].team
			} else {
				teamObject.nickname = teamGames[nikcindex].team_a['0'].name
			}

		} else if (teamGames[nikcindex].team_b['0'].owner === team.name) {
			if (teamGames[nikcindex].team_b['0'].team) {
				teamObject.nickname = teamGames[nikcindex].team_b['0'].team
			} else {
				teamObject.nickname = teamGames[nikcindex].team_b['0'].name
			}
		}

		teamGames.sort(function (a, b) {
			a = new Date(a.date);
			b = new Date(b.date);
			return a > b ? -1 : a < b ? 1 : 0;
		});

		teamGames.reverse();

		var gameindex = teamGames.findIndex(game => {
			if (game.team_a["0"].owner === team.name || game.team_b["0"].owner === team.name) {
				return game.year;
			}
		});

		teamObject.founded = teamGames[gameindex].year;

		// yearly history build up 

		var yearly = [];
		teamObject.totalWins = 0;
		teamObject.totalLoses = 0;
		teamObject.totalTies = 0;
		teamGames.map((game) => {
			if (
				(parseFloat(game.team_a[0].score) > parseFloat(game.team_b[0].score)) && game.team_a[0].owner === team.name ||
				(parseFloat(game.team_b[0].score) > parseFloat(game.team_a[0].score)) && game.team_b[0].owner === team.name
			) {
				teamObject.totalWins++;
				if (yearly.find(obj => obj.year === parseInt(game.year))) {
					yearly.map((yearsg, indi) => {
						if (yearsg.year === parseInt(game.year)) {
							yearly[indi].wins++
							yearly[indi].points += 10;
						}
					})
				} else {
					yearly.push({
						year: parseInt(game.year),
						wins: 1,
					});
				}
			} else if (parseFloat(game.team_a[0].score) === parseFloat(game.team_b[0].score)) {
				teamObject.totalTies++;
			} else {
				teamObject.totalLoses++;
			}
		})

		teamObject.yearly = yearly.sort((a, b) => a.year - b.year);

		var yearsActive = [];
		yearly.map((year) => {
			yearsActive.push(year.year);
		})
		teamObject.yearsActive = yearsActive.sort((a, b) => a - b);

		// number of seasons
		teamObject.seasons = yearly.length;

		// logo 
		teamObject.logo = team.image

		// current stats 
		teamObject.winStreak = 0,
		teamObject.lossStreak = 0,
		teamObject.lossStart = "",
		teamObject.lossEnd = "",
		teamObject.winStart = "",
		teamObject.winEnd = "",
		teamObject.highWinMarginA = 0,
		teamObject.highWinMarginB = 0,
		teamObject.lowWinMarginA = 10000,
		teamObject.lowWinMarginB = 10000,
		teamObject.highLossMarginA = 0,
		teamObject.highLossMarginB = 0,
		teamObject.lowLossMarginA = 10000,
		teamObject.lowLossMarginB = 10000,
		teamObject.stats = {
			highestStreak: 0,
			lowestStreak: 0,
			currentStreak: 0,
			highestScore: 0,
			lowestScore: 10000,
			highestWinMargin: 0,
			lowestWinMargin: 10000,
			highestLossMargin: 0,
			lowestLossMargin: 10000,
			totalPoints: 0,
			totalPointsAgainst: 0,
			lossStart: "",
			lossEnd: "",
			winStart: "",
			winEnd: "",
		}

		for (var ii = 0; ii < teamGames.length; ii++) {
			const week = teamGames[ii].week.split(" ");
			teamGames[ii].date = "01/" + week[1] + "/" + parseInt(teamGames[ii].year);
		}

		teamGames.sort(function (a, b) {
			a = new Date(a.date);
			b = new Date(b.date);
			return a > b ? -1 : a < b ? 1 : 0;
		});

		teamGames.reverse();

		var worstTeam = {};
		var rankHigh = 0;
		var testArray = [];
		var mySet = new Set();

		console.log(yearly);

		games.map((game) => {
			// Breting
			
			//console.log(year.year);
			
			
			if (mySet.has(game.team_a[0].owner)) {

			}
			else {
				mySet.add(game.team_a[0].owner);
			}

			//testArray.push(mySet);
			//console.log(testArray);
		})

		

		teamGames.map((game, inde) => {
			var recordA = game.team_a[0].record.split("-");
			var rankA = recordA[recordA.length - 1].split(" ");
			var rankATrimmed = parseInt(rankA[1].match(/\(([^)]+)\)/)[1]);

			var recordB = game.team_b[0].record.split("-");
			var rankB = recordB[recordB.length - 1].split(" ");
			var rankBTrimmed = parseInt(rankB[1].match(/\(([^)]+)\)/)[1]);

			if (rankATrimmed > rankBTrimmed && rankATrimmed > rankHigh) {
				rankHigh = rankATrimmed;
				worstTeam = game.team_a[0];
				//console.log("rankHigh:" + rankHigh  + "Year:" + game.year + "Owner:" + worstTeam.owner);
			}
			else if (rankBTrimmed > rankHigh) {
				rankHigh = rankBTrimmed;
				worstTeam = game.team_b[0];
				//console.log("rankHigh:" + rankHigh + "Year:" + game.year + "Owner:" + worstTeam.owner);
			}

			
			if (game.team_a[0].owner === team.name &&
				parseFloat(game.team_a[0].score) > parseFloat(game.team_b[0].score)
				||
				game.team_b[0].owner === team.name &&
				parseFloat(game.team_b[0].score) > parseFloat(game.team_a[0].score)
			) {

				teamObject.lowWinMarginA = parseFloat(game.team_a[0].score) - parseFloat(game.team_b[0].score);
				teamObject.lowWinMarginB = parseFloat(game.team_b[0].score) - parseFloat(game.team_a[0].score);
				teamObject.highWinMarginA = parseFloat(game.team_b[0].score) - parseFloat(game.team_a[0].score);
				teamObject.highWinMarginB = parseFloat(game.team_a[0].score) - parseFloat(game.team_b[0].score);
				if (teamObject.lossStreak != 0) teamObject.lossEnd = game.year.split(" ")[0] + " " + game.week;
				if (teamObject.winStreak === 0) teamObject.winStart = game.year.split(" ")[0] + " " + game.week;

				if (teamObject.lossStreak === teamObject.stats.lowestStreak && teamObject.lossStreak != 0) {
					teamObject.stats.lossEnd = teamObject.lossEnd;
					teamObject.lossEnd = "";
				}
				teamObject.winStreak++;
				teamObject.lossStreak = 0;
			} else if (game.team_a[0].owner === team.name && parseFloat(game.team_a[0].score) < parseFloat(game.team_b[0].score)
				||
				game.team_b[0].owner === team.name && parseFloat(game.team_b[0].score) < parseFloat(game.team_a[0].score)
			) {
				//console.log(game.year + game.week);
				teamObject.lowLossMarginA = parseFloat(game.team_b[0].score) - parseFloat(game.team_a[0].score);
				teamObject.lowLossMarginB = parseFloat(game.team_a[0].score) - parseFloat(game.team_b[0].score);
				teamObject.highLossMarginA = parseFloat(game.team_b[0].score) - parseFloat(game.team_a[0].score);
				teamObject.highLossMarginB = parseFloat(game.team_a[0].score) - parseFloat(game.team_b[0].score);
				if (teamObject.winStreak != 0) teamObject.winEnd = game.year.split(" ")[0] + " " + game.week;
				if (teamObject.lossStreak === 0) teamObject.lossStart = game.year.split(" ")[0] + " " + game.week;
				if (teamObject.winStreak === teamObject.stats.highestStreak && teamObject.winStreak != 0) {
					teamObject.stats.winEnd = teamObject.winEnd;
					teamObject.winEnd = "";
				}

				teamObject.lossStreak++;
				teamObject.winStreak = 0;
			} else {
				if (teamObject.winStreak === 0) teamObject.winStart = game.year.split(" ")[0] + " " + game.week;
				if (teamObject.lossStreak != 0) teamObject.lossEnd = game.year.split(" ")[0] + " " + game.week;
				if (teamObject.winStreak === teamObject.stats.highestStreak && teamObject.winStreak != 0) {
					teamObject.stats.winEnd = teamObject.winEnd;
					teamObject.winEnd = "";
				}
				teamObject.lossStreak = 0;
				teamObject.winStreak = 0;
			}

			// set currentStreak
			if (teamObject.winStreak === 0) {
				teamObject.stats.currentStreak = teamObject.lossStreak + "L";
			}
			else {
				teamObject.stats.currentStreak = teamObject.winStreak + "W";
			}

			// set the master stats var
			if (teamObject.winStreak >= teamObject.stats.highestStreak) {
				teamObject.stats.highestStreak = teamObject.winStreak;
				teamObject.stats.winStart = teamObject.winStart;
				if (teamObject.stats.currentStreak === teamObject.stats.highestStreak + "W") {
					teamObject.stats.winEnd = "Current";
				}
			}

			if (teamObject.lossStreak >= teamObject.stats.lowestStreak) {
				teamObject.stats.lowestStreak = teamObject.lossStreak;
				teamObject.stats.lossStart = teamObject.lossStart;
				if (teamObject.stats.currentStreak === teamObject.stats.lowestStreak + "L") {
					teamObject.stats.lossEnd = "Current";
				}
			}

			// Check Highest score
			if (parseFloat(game.team_a[0].score) > teamObject.stats.highestScore && game.team_a[0].owner === team.name) {
				teamObject.stats.highestScore = parseFloat(game.team_a[0].score);
			}

			if (parseFloat(game.team_b[0].score) > teamObject.stats.highestScore && game.team_b[0].owner === team.name) {
				teamObject.stats.highestScore = parseFloat(game.team_b[0].score);
			}

			// Check lowest score 
			if (parseFloat(game.team_a[0].score) < teamObject.stats.lowestScore && game.team_a[0].owner === team.name) {
				teamObject.stats.lowestScore = parseFloat(game.team_a[0].score);
			}

			if (parseFloat(game.team_b[0].score) < teamObject.stats.lowestScore && game.team_b[0].owner === team.name) {
				teamObject.stats.lowestScore = parseFloat(game.team_b[0].score);
			}

			// Check Highest margin 

			if (teamObject.highWinMarginA > teamObject.stats.highestWinMargin && game.team_a[0].owner === team.name) {
				teamObject.stats.highestWinMargin = teamObject.highWinMarginA;
			}

			if (teamObject.highWinMarginB > teamObject.stats.highestWinMargin && game.team_b[0].owner === team.name) {
				teamObject.stats.highestWinMargin = teamObject.highWinMarginB;
			}

			if (teamObject.highLossMarginA > teamObject.stats.highestLossMargin && game.team_a[0].owner === team.name) {
				teamObject.stats.highestLossMargin = teamObject.highLossMarginA;
			}

			if (teamObject.highLossMarginB > teamObject.stats.highestLossMargin && game.team_b[0].owner === team.name) {
				teamObject.stats.highestLossMargin = teamObject.highLossMarginB;
			}


			// Check Lowest margin 
			if (teamObject.lowWinMarginA < teamObject.stats.lowestWinMargin && game.team_a[0].owner === team.name && teamObject.lowWinMarginA > 0) {
				teamObject.stats.lowestWinMargin = teamObject.lowWinMarginA;
			}

			if (teamObject.lowWinMarginB < teamObject.stats.lowestWinMargin && game.team_b[0].owner === team.name && teamObject.lowWinMarginB > 0) {
				teamObject.stats.lowestWinMargin = teamObject.lowWinMarginB;
			}

			if (teamObject.lowLossMarginA < teamObject.stats.lowestLossMargin && game.team_a[0].owner === team.name && teamObject.lowLossMarginA > 0) {
				teamObject.stats.lowestLossMargin = teamObject.lowLossMarginA;
			}

			if (teamObject.lowLossMarginB < teamObject.stats.lowestLossMargin && game.team_b[0].owner === team.name && teamObject.lowLossMarginB > 0) {
				teamObject.stats.lowestLossMargin = teamObject.lowLossMarginB;
			}

			if (game.team_a[0].owner === team.name) {
				teamObject.stats.totalPoints += parseFloat(game.team_a[0].score);
				teamObject.stats.totalPointsAgainst += parseFloat(game.team_b[0].score);

			}

			if (game.team_b[0].owner === team.name) {
				teamObject.stats.totalPoints += parseFloat(game.team_b[0].score);
				teamObject.stats.totalPointsAgainst += parseFloat(game.team_a[0].score);
			}
		})

		if (worstTeam.owner === team.name) {
			teamObject.regularSeasonLastPlace++
		}
		teamObject.averageScore = teamObject.stats.totalPoints / teamGames.length;
		teamObject.averageScoreAgaints = teamObject.stats.totalPointsAgainst / teamGames.length;

		//console.log(' loaded team data ', teamObject);
		return teamObject
	}

	loadChart = () => {
		const dataSetCopy = { ...this.state.dataSet };
		dataSetCopy.data = []
		for (var i = 0; i < this.state.teamSelected.yearly.length; i++) {
			dataSetCopy.data.push(this.state.teamSelected.yearly[i].wins)
		}

		this.setState({ dataSet: dataSetCopy })
	}

	componentWillMount() {
		const source = this.props.data.allDataJson.edges[0].node.owners;
		var team = this.state.teamSelected;
		var teams = [];
		for (var i = 0; i < source.length; i++) {
			teams.push(source[i].name);
		}
		team = source[0];

		const years = this.props.data.allDataJson.edges[0].node.years;
		var games = [];
		years.map((year, index) => {
			year.weeks_games.map((week, inde) => {
				games.push(...week.games);
			})
		})
		this.setState({ allGames: games })
		this.setState({ allTeams: teams })
		this.setState({ teamSelected: this.loadTeamData(team, games) })
		//console.log('will mount', this.state.teamSelected );
	}

	componentDidMount() {
		const localOwners = [...this.state.allTeams];
		const allGames = this.state.allGames;
		const allGamesPlayers = [];
		allGames.map((game, index) => {
			allGamesPlayers.push(game.team_a[0].owner)
			allGamesPlayers.push(game.team_b[0].owner)
		})

		const uniqueNames = allGamesPlayers.filter((val, id, array) => array.indexOf(val) == id);

		uniqueNames.map((name, index) => {
			if (localOwners.indexOf(name) === -1) {
				localOwners.push(name + " (inactive)");
			}
		})
		this.setState({ allTeams: localOwners })
		this.setState({ teamSelected: this.loadTeamData(this.state.teamSelected, allGames) })
		//console.log('did mount', this.state.allTeams, localOwners );

		this.loadChart();
	}

	render() {
		//console.log('props', this.props, this.state);

		const chartData = {
			labels: this.state.teamSelected.yearsActive,
			datasets: [this.state.dataSet]
		}
		return (
			<main className="container-fluid">
				<Breadcrumb title="Franchise Stats" />
				<div className="row">
					<div className="col-xl-5 col-lg-12">
						<TeamStats data={this.state.teamSelected} teams={this.state.allTeams} change={(event) => this.teamChangeHandler(event)} />
					</div>
					<div className="col-xl-7 col-lg-12">
						<Performance
							title="Regular Season"
							rate={this.rate((this.state.teamSelected.totalWins - this.state.teamSelected.playOffWins), (this.state.teamSelected.totalLoses - this.state.teamSelected.playOffLoses), (this.state.teamSelected.totalTies - this.state.teamSelected.playOffTies))}
							win={this.state.teamSelected.totalWins - this.state.teamSelected.playOffWins}
							loss={this.state.teamSelected.totalLoses - this.state.teamSelected.playOffLoses}
							tie={this.state.teamSelected.totalTies - this.state.teamSelected.playOffTies}
							boxa={{ title: 'Regular Season Champion', content: this.state.teamSelected.regularSeasonChampion }}
							boxb={{ title: 'Most Points', content: '4' }}
							boxc={{ title: 'Last Place', content: this.state.teamSelected.regularSeasonLastPlace }}
						/>
						<Performance
							title="Playoff"
							rate={this.rate(this.state.teamSelected.playOffWins, this.state.teamSelected.playOffLoses, this.state.teamSelected.playOffTies)}
							win={this.state.teamSelected.playOffWins}
							loss={this.state.teamSelected.playOffLoses}
							tie={this.state.teamSelected.playOffTies}
							boxa={{ title: 'Playoffs Appearances', content: this.state.teamSelected.playoffAppearances }}
							boxb={{ title: 'Championships Appearance', content: this.state.teamSelected.championshipAppearance }}
							boxc={{ title: 'Championships', content: this.state.teamSelected.championshipWins }}
						/>
					</div>
				</div>
				<Chart data={chartData} />
			</main>);
	}
}
export default IndexPage;

export const pageQuery = graphql`
  query IndexQuery {
		allDataJson {
			edges {
				node {
					owners {
						name
						waiver
						image
						fantasyname
					}
					years{
						weeks_games {
							games {
								week
								team_a {
									team
									team_url
									owner
									logo
									score
									record
									streak
									waiver
									bench_total
								}
								team_b {
									name
									url
									owner
									logo
									score
									record
									streak
									waiver
									bench_points
								}
								year
							}
						}
					}
				} 
			}
		}
}
`;



